header {
  grid-area: header;
  background: whitesmoke;
  padding: 20px; }

map {
  grid-area: content;
  background: linear-gradient(to bottom, whitesmoke, white);
  padding: 20px; }

shopping {
  grid-area: shopping;
  background: linear-gradient(to bottom, whitesmoke, white); }

footer {
  padding: 20px;
  grid-area: footer;
  background: white; }

footer img {
  width: 80px;
  display: block;
  margin-left: auto;
  margin-right: auto; }

.wrapper {
  border: 5px solid #000;
  border-radius: 5px;
  box-sizing: border-box;
  display: grid;
  grid-gap: 0px;
  grid-template-columns: 3fr;
  grid-template-areas: 'header       header' 'content      content' 'shopping     shopping' 'footer       footer'; }
